<template>
  <div id="app">
    <div class="main">
      <!--  start -->
      <div class="title"> <span class="block">data</span></div>
      <div class="content">
        <vCustomSteps :data="data"  style="height: 600px;"></vCustomSteps>
      </div>
      <!--  end -->
      <!--  start -->
      <div class="title"> , <span class="block">direction</span>,
        <span class="block">horizontal</span><span class="block">vertical</span></div>
      <div class="contentFlex">
        <div class="contentLeft">
          <vCustomSteps :data="data" direction="horizontal"  style="height: 600px; "></vCustomSteps>
        </div>
        <div class="contentRight">
          <vCustomSteps :data="data" direction="vertical"  style="height: 600px; "></vCustomSteps>
        </div>
      </div>
      <!--  end -->
      <!-- theme start -->
      <div class="title"> <span class="block">theme='#800080'</span> ,configStyletheme<span class="block">configStyle</span></div>
      <div class="content">
        <vCustomSteps :data="data" theme="#1890ff"  :configStyle='config2'  direction="horizontal"  style="height: 600px; "></vCustomSteps>
      </div>
      <!-- theme end -->
      <!--  start -->
      <div class="title"> <span class="block">align-center</span>  <span class="block"> direction="horizontal"</span>  </div>
      <div class="content">
        <vCustomSteps :data="data" align-center  direction="horizontal" style="height: 600px; "></vCustomSteps>
      </div>
      <!--  end -->
      <!--  start -->
      <div class="title">,<span class="block">configStyle</span>dom<span class="block">@clickItem="testClick"</span><span class="block"> mediumLineStyle</span>
      </div>
      <div class="content">
        <vCustomSteps :data="data" @clickItem="testClick"   :configStyle='config1'   direction="horizontal"  style="height: 600px; "></vCustomSteps>
      </div>

      <!--  end -->

      <!--  start -->
      <div class="title">, <span class="block">data</span></div>
      <div class="content">
        <vCustomSteps :data="oDataAnyone" direction="horizontal"  style="height: 600px; "></vCustomSteps>
      </div>
      <!--  end -->

      <!--  start -->
      <div class="title">, <span class="block">+</span></div>
      <div class="content">
        <vCustomSteps :data="data"  style="height: 600px;">
          <!--  -->
          <template :slot='`step${index}`' v-for='(item,index) in data'><span :key='item'>{{index+1}}</span></template>

          <!-- key -->
          <template :slot='`key${index}`' v-for='(item,index) in data'><span :key='item'>{{item.name}}</span></template>

          <!-- value -->
          <template :slot='`value${index}`' v-for='(item,index) in data'><span :key='item'>{{item.value}}</span></template>

        </vCustomSteps>
      </div>
      <!--  end -->

    </div>
  </div>
</template>

<script>
import data from '../mock/step.json'
import oDataAnyone from '../mock/step.json'

import vCustomSteps from '../src/lib/v-custom-steps.vue'
export default {
  name: 'app',
  components: {
    vCustomSteps
  },
  data () {
    return {
      data: data,
      oDataAnyone: oDataAnyone,
      config2: {
        circleStyle: {
          color: 'rgb(239, 13, 239)'
        }
      },
      config1: {
        // 
        mediumLineStyle: {
          color: '#fff',
          width: '8px'
        },
        // 
        nodeLineStyle: {
          color: 'yellow'
        },
        // 
        textStyle: {
          color: 'green'
        },
        // 
        circleStyle: {
          width: '100px',
          height: '100px',
          lineHeight: '100px',
          backgroundColor: 'pink',
          boxShadow: '0px 0px 15px orange inset',
          color: 'purple'
        }
      }

    }
  },
  methods: {
    testClick (e, item, index) {
      console.log('~~', e, item, index)
    }
  }

}
</script>

<style>
html,body{
  width: 100%;
  height: 100%;
  background:rgba(1, 3, 46, 1);
}
blockquote, body, button, dd, dl, dt, fieldset, form, h1, h2, h3, h4, h5, h6, hr, input, legend, li, ol, p, pre, td, textarea, th, ul{
  margin: 0;
  padding: 0;
}
#app {
  height: 100%;
  width: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.content{
  width: 90%;
  margin: 10px auto;
  background: rgba(255,255,255,0.05)
}
.main{
  padding: 16px;
}
.title{
  height: 30px;
  line-height: 30px;
  background: rgba(255,255,255,0.05);
  font-size: 16px;
  color: #fff;
  font-weight: 600;
  border-left: 2px solid #259ee3;
  padding-left: 4px;
  margin: 16px 0;
}
.block{
  color: coral;
  background: #867f7f61;
  padding: 0 16px;
  display: inline-block;
  border-radius: 5px;
  font-weight: 500;
  margin: 0 5px;
}
.contentFlex{
  display: flex;
}
.contentLeft{
  width: 49%;
  border-right: 1px dashed darkviolet;
}
.contentLeft{
  width: 49%;
}
</style>



